$icons: (0:0)
$icons: map-merge($icons,(apple-lang: (X: -28px, Y:-30px, W: 16px, H: 16px, TW: 81px, TH: 65px, IMG: '../img/icons.png')))
$icons: map-merge($icons,(arr-down: (X: 0px, Y:-55px, W: 15px, H: 10px, TW: 81px, TH: 65px, IMG: '../img/icons.png')))
$icons: map-merge($icons,(cart: (X: -56px, Y:-23px, W: 20px, H: 19px, TW: 81px, TH: 65px, IMG: '../img/icons.png')))
$icons: map-merge($icons,(mail: (X: -56px, Y:0px, W: 25px, H: 19px, TW: 81px, TH: 65px, IMG: '../img/icons.png')))
$icons: map-merge($icons,(pass: (X: 0px, Y:0px, W: 24px, H: 26px, TW: 81px, TH: 65px, IMG: '../img/icons.png')))
$icons: map-merge($icons,(search: (X: 0px, Y:-30px, W: 24px, H: 21px, TW: 81px, TH: 65px, IMG: '../img/icons.png')))
$icons: map-merge($icons,(user: (X: -28px, Y:0px, W: 24px, H: 25px, TW: 81px, TH: 65px, IMG: '../img/icons.png')))


// Gets an attribute from the sass map
@function icon-attr($icon, $attr)
	$icon: map-get($icons, $icon)
	@return map-get($icon, $attr)

@mixin sprite($iconName)
	background-image: url(icon-attr($iconName, IMG))
	width: icon-attr($iconName, W)
	height: icon-attr($iconName, H)
	background-position: icon-attr($iconName, X) icon-attr($iconName, Y)
@mixin sprite-position($iconName)
	background-position: icon-attr($iconName, X) icon-attr($iconName, Y)
@mixin sprite-retina($iconName)
	background-image: url(icon-attr($iconName, IMG))
	$width: icon-attr($iconName, W)
	$height: icon-attr($iconName, H)
	width: $width/2
	height: $height/2
	$x: icon-attr($iconName, X)
	$y: icon-attr($iconName, Y)
	background-position: $x/2 $y/2
	$tw: icon-attr($iconName, TW) 	
	$th: icon-attr($iconName, TH)
	background-size: $tw/2 $th/2  	


@mixin s($i)
	+sprite($i)
@mixin sp($i)
	+sprite-position($i)
@mixin sr($i)
	+sprite-retina($i)	

